import React, { Fragment, useState } from 'react';
import { Typography, TypographyProps, useTheme } from '@material-ui/core';

import { SxProps } from '@material-ui/system';
import { useAppTheme } from '../context/AppThemeContext';

const LETTER_SPACING = '0.25em';

type FontVariant = 'normal' | 'title' | 'titleLight' | 'subtitle';

const fontVariantStyles: Record<FontVariant, SxProps> = {
  normal: {},
  title: {
    fontWeight: 900,
    fontSize: '1.5em',
    fontFamily: '"Raleway", Helvetica, sans-serif',
    letterSpacing: LETTER_SPACING,
  },
  titleLight: {
    fontWeight: 800,
    fontSize: '0.8em',
    fontFamily: '"Raleway", Helvetica, sans-serif',
    letterSpacing: LETTER_SPACING,
  },
  subtitle: {
    fontWeight: 400,
    fontSize: '0.7em',
    fontFamily: '"Raleway", Helvetica, sans-serif',
    letterSpacing: LETTER_SPACING,
  },
};

export interface IStyledTypographyProps extends TypographyProps {
  bold?: boolean;
  capitalized?: boolean;
  spaced?: boolean;
  fontVariant?: FontVariant;
  hoverEffect?: boolean;
}

const StyledTypography = ({
  bold = false,
  capitalized = false,
  spaced = false,
  fontVariant = 'normal',
  hoverEffect = false,
  children,
  sx: fontSxProps,
  ...rest
}: IStyledTypographyProps & TypographyProps) => {
  const { theme } = useAppTheme();
  const [isHover, setIsHover] = useState(false);
  const fontVariantStyle = fontVariantStyles[fontVariant];
  const capitalizedChildren =
    capitalized && typeof children === 'string'
      ? children?.toString()?.toUpperCase()
      : children;
  const sx = { ...fontSxProps, ...fontVariantStyle };
  const onMouseEnter = () => {
    setIsHover(true);
  };

  const onMouseLeave = () => {
    setIsHover(false);
  };
  return (
    <Typography
      {...rest}
      onMouseEnter={onMouseEnter}
      onMouseLeave={onMouseLeave}
      fontWeight={bold ? 'bold' : undefined}
      letterSpacing={spaced ? LETTER_SPACING : undefined}
      sx={{
        ...sx,
        ...(isHover && hoverEffect
          ? {
              color: theme.palette.primary.main,
            }
          : {}),
      }}>
      {capitalizedChildren}
    </Typography>
  );
};

export default StyledTypography;
