import { BrowserRouter, Route, Switch } from 'react-router-dom';
import React, { Suspense, lazy } from 'react';

import { CssBaseline } from '@material-ui/core';
import Main from './layout/Main'; // fallback for lazy pages
import { PageContentLayoutPlaceholder } from './layout/PageContentLayout';
import ProvideAppTheme from './context/AppThemeContext';
import { StyledEngineProvider } from '@material-ui/core/styles';

const { PUBLIC_URL } = process.env;

// Every route - we lazy load so that each page can be chunked
// NOTE that some of these chunks are very small. We should optimize
// which pages are lazy loaded in the future.
const Home = lazy(() => import('./pages/Home'));
const About = lazy(() => import('./pages/About'));
const Resume = lazy(() => import('./pages/Resume'));
const Projects = lazy(() => import('./pages/Projects'));
const Stats = lazy(() => import('./pages/Stats'));
const Contact = lazy(() => import('./pages/Contact'));
const NotFound = lazy(() => import('./pages/NotFound'));

const App = () => {
  return (
    <>
      <ProvideAppTheme>
        <CssBaseline>
          <StyledEngineProvider injectFirst>
            <BrowserRouter basename={PUBLIC_URL}>
              <Main>
                <Suspense fallback={<PageContentLayoutPlaceholder />}>
                  <Switch>
                    <Route exact path="/" component={Home} />
                    <Route path="/about" component={About} />
                    <Route path="/resume" component={Resume} />
                    <Route path="/projects" component={Projects} />
                    <Route path="/stats" component={Stats} />
                    <Route path="/contact" component={Contact} />
                    <Route
                      path="/ph"
                      component={PageContentLayoutPlaceholder}
                    />
                    <Route component={NotFound} />
                  </Switch>
                </Suspense>
              </Main>
            </BrowserRouter>
          </StyledEngineProvider>
        </CssBaseline>
      </ProvideAppTheme>
    </>
  );
};

export default App;
