import {
  Link,
  LinkProps,
  Typography,
  TypographyProps,
  useTheme,
} from '@material-ui/core';
import React, { ReactNode, useState } from 'react';

import { useAppTheme } from '../context/AppThemeContext';

export interface IStyledLinkLinkProps extends Omit<LinkProps, 'href'> {
  to: string;
  label: string;
  capitalized?: boolean;
  bold?: boolean;
  bottomBorder?: boolean;
  typographyProps?: Omit<TypographyProps, 'children'>;
  children?: JSX.Element;
}

const StyledLink = ({
  to,
  label,
  capitalized,
  bold,
  bottomBorder = true,
  typographyProps,
  children,
  ...rest
}: IStyledLinkLinkProps) => {
  const { theme } = useAppTheme();
  const [isHover, setIsHover] = useState(false);
  const transformedLabel = capitalized ? label?.toUpperCase() : label;
  const overriddenStyle: React.CSSProperties = {
    transition: 'color 0.2s ease, border-bottom-color 0.2s ease',
    borderBottom: bottomBorder
      ? 'dotted 1px rgba(160, 160, 160, 0.65)'
      : undefined,
    fontWeight: bold ? 'bold' : undefined,
    cursor: 'pointer',
  };
  const onMouseEnter = () => {
    setIsHover(true);
  };

  const onMouseLeave = () => {
    setIsHover(false);
  };
  return (
    <Link
      {...rest}
      href={to}
      style={{
        color: 'inherit',
        textDecoration: 'none',
      }}>
      {children &&
        React.Children.map(children, (child) =>
          React.cloneElement(child, {
            onMouseEnter: onMouseEnter,
            onMouseLeave: onMouseLeave,
            style: {
              ...child.props.style,
              ...overriddenStyle,
              ...(isHover
                ? {
                    color: theme.palette.primary.main,
                  }
                : {}),
            },
          }),
        )}
      {!children && (
        <Typography
          {...typographyProps}
          onMouseEnter={onMouseEnter}
          onMouseLeave={onMouseLeave}
          style={{
            ...overriddenStyle,
            ...(isHover
              ? {
                  color: theme.palette.primary.main,
                }
              : {}),
          }}
          component="span">
          {transformedLabel}
        </Typography>
      )}
    </Link>
  );
};

export default StyledLink;
