import { NavLink, NavLinkProps } from 'react-router-dom';
import React, { ReactNode, useState } from 'react';
import { Typography, TypographyProps, useTheme } from '@material-ui/core';

import { useAppTheme } from '../context/AppThemeContext';

interface IStyledLinkLinkProps extends NavLinkProps {
  to: string;
  label: string;
  capitalized?: boolean;
  bold?: boolean;
  bottomBorder?: boolean;
  typographyProps?: Omit<TypographyProps, 'children'>;
  disableActiveStyle?: boolean;
  children?: JSX.Element;
}

const StyledNavLink = ({
  to,
  label,
  capitalized,
  bold,
  bottomBorder = true,
  typographyProps,
  children,
  activeStyle,
  disableActiveStyle = false,
  ...rest
}: IStyledLinkLinkProps) => {
  const { theme } = useAppTheme();
  const [isHover, setIsHover] = useState(false);
  const transformedLabel = capitalized
    ? label?.toString()?.toUpperCase()
    : label;
  const { style: typographyStyle, ...restOfTypographyProps } = {
    ...typographyProps,
  };
  const overriddenStyle: React.CSSProperties = {
    ...typographyStyle,
    transition: 'color 0.2s ease, border-bottom-color 0.2s ease',
    borderBottom: bottomBorder
      ? 'dotted 1px rgba(160, 160, 160, 0.65)'
      : undefined,
    fontWeight: bold ? 'bold' : undefined,
  };
  const onMouseEnter = () => {
    setIsHover(true);
  };

  const onMouseLeave = () => {
    setIsHover(false);
  };
  return (
    <NavLink
      {...rest}
      to={to}
      style={{
        color: 'inherit',
        textDecoration: 'none',
      }}
      activeStyle={
        disableActiveStyle
          ? {}
          : {
              textDecoration: 'underline',
              color: theme.palette.primary.main,
              ...activeStyle,
            }
      }>
      {children &&
        React.Children.map(children, (child) =>
          React.cloneElement(child, {
            onMouseEnter: onMouseEnter,
            onMouseLeave: onMouseLeave,
            style: {
              ...child.props.style,
              ...(isHover
                ? {
                    color: theme.palette.primary.main,
                  }
                : {}),
            },
          }),
        )}
      {!children && (
        <Typography
          {...typographyProps}
          onMouseEnter={onMouseEnter}
          onMouseLeave={onMouseLeave}
          component="span"
          style={{
            ...overriddenStyle,
            ...(isHover
              ? {
                  color: theme.palette.primary.main,
                }
              : {}),
          }}>
          {transformedLabel}
        </Typography>
      )}
    </NavLink>
  );
};

export default StyledNavLink;
